header{
    height: 100vh;
    padding-top: 1rem;
    overflow: hidden ;
}

.header__links{
    
    display: block;
    background: var(--color-primary);
    padding: 0.6rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 0rem;
    height: 3rem;
    
} 

.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 3rem;
    margin: 0 auto 0.6rem;
    
}

ul:first-child {
    text-decoration: underline;
}

.permalinks a{
    color: var(--color-bg);
    font-weight: 500;    
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

.header__basics{
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
}
/*===================CTA=================*/

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;

}

.CTA__Lang{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: right;

}

/*==================HEADER SOCIAL==============*/

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: "";
    width: 2px;
    height: 4rem;
    background: var(--color-primary);
}


/*==================IMAGE==============*/


.me{
    background: linear-gradient( var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem ;

}


.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 200;
    font-size: 0.9rem;

}


/* --------------Media queries (medium devices)---------------------------*/
@media screen and (max-width: 1024px) {
    
        
    header{
        height: 68vh;
    }

    .header__links{
    
        display: none;
        
    } 
    

}

/* --------------Media queries (small devices)---------------------------*/
@media screen and (max-width: 600px) {
    
    header{
        height: 100vh;
    }

    .header__links{
    
        display: none;
        
    } 
    
    .header__socials, 
    .scroll__down{
        display: none;
    }
}
