.portfolio__container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;

}

.portfolio__item{
    background: var(--color-bg-variant);
    padding: 1rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);

}

.wrapper {
    position: relative;
    display: inline-block;
    height: 82%;
}

h4{
    font-weight: 300;
    font-size: 0.9rem;
}

.preview{
    z-index: 1;
    position: relative;
    top: 0;
    left: 0;
    display: block;
  
}

.portfolio__item .detail{
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    text-align: center; 
    background: var(--color-bg);
    opacity: 0.97;   
    height: 100%;
    width: 100%;
    border-radius: 0.8rem;
    display: none;
}

.portfolio__item .detail .portfolio__item-image{
    width: 40%;
}

.portfolio__item:hover .preview{
    /* display: none; */
}



.portfolio__item:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio__item:hover .detail{
    text-align: center;
    display: block;
}

.portfolio__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3{
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta{
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}

/* --------------Media queries (medium plus devices)---------------------------*/
@media screen and (max-width: 1490px) {
    
    h4{
        font-size: 0.8rem;
    }
    .wrapper {
        height: 80%;
    }
    

}

/* --------------Media queries (medium plus devices)---------------------------*/
@media screen and (max-width: 1360px) {
    h4{
        font-size: 0.7rem;
    }
    .wrapper {
        height: 80%;
    }
}

/* --------------Media queries (medium devices)---------------------------*/
@media screen and (max-width: 1024px) {
    .portfolio__container{
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }

    h4{
        font-size: 1.5vw;
    }

}

/* --------------Media queries (small devices)---------------------------*/
@media screen and (max-width: 600px) {
    .portfolio__container{
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    h4{
        font-size: 3vw;
    }
    
}